
$font : Arial,
Helvetica,
sans-serif;
$main-color: #F1F5FA;
$border-color: #eaebed;
$button-color: #5275E9;
$text-color: #626161;
$blue : #39B0DC;
$red : #F15562;
$green : #4FC9A2;

* {
	box-sizing: border-box;
	font-family: $font;
	margin: 0;
	padding: 0;
}

body {
	background-color: $main-color;
}

.outerContainer {
	padding: 1%;

	.info {
		font-weight: 400;
		color: $text-color;
		word-spacing: 5px;
		margin: 2vw;
	}

	.filterArea {
		position: relative;
		padding: 2.66% 1% 2.66% 1%;
		margin: 0% 1% 2% 1%;
		background-color: #FFFFFF;
		border: 3px solid $border-color;

		.proceedButton {
			margin: 0 0 0 40%;
			background-color: $button-color;
			border: none;
			border-radius: 5px;
			font-size: 20px;
			color: #FFFFFF;
			padding: 10px;
			width: 8%;
		}

		.proceedButton:disabled {
			opacity: 50%;
		}

		.searchField {
			display: inline-block;
			vertical-align: top;
			background-color: #FFFFFF;
			width: 50%;
			height: 7.5em;

			.schemeLabel {
				font-size: 1.3rem;
			}

			.dropdownInput {
				position: relative;
				margin: 0.5% 0% 0% 0%;
				padding: 1.2%;
				display: block;
				width: 73.5%;
				border: 3px solid #eaebed;
				border-radius: 4px;
				height: 33%;
				font-size: 16px;
			}

			input[type="search"]::-webkit-search-cancel-button {
				cursor: pointer;
			}

			.dropDown {
				border: 1px solid grey;
				position: absolute;
				width: 35.7%;
				margin: 0% 0 0 0.1%;
				display: block;
				overflow-y: auto;
				max-height: 18em;
				z-index: 1;
				background-color: #ffffff;

				.dropDown-row {
					cursor: pointer;
					text-align: start;
					margin: 2px 0;
					background-color: white;
					padding: 4px;
					margin-bottom: 1px;
					font-size: 18px;
				}

				.dropDown-row:hover {
					background-color: #efeff0;
				}
			}

			.dropDown:empty {
				border: none;
			}
		}
	}

	.portfolioOverlap {
		margin: 1%;
		width: 98%;
		height: 27vw;
		background-color: #FFFFFF;
		border: 3px solid $border-color;
		display: block;
		position: relative;
		z-index: 0;

		.leftDiv {
			position: absolute;
			background-color: #ffffff;
			border: 1px solid $border-color;
			border-radius: 2%;
			width: 42.58vw;
			margin: 1.5% 0% 0% 1.5%;
			height: 24vw;

			.vennDiagramLeftLabel {
				display: inline-block;
				position: absolute;
				background-color: $blue;
				border-radius: 4px;
				color: #FFFFFF;
				padding: 0.5rem;
				width: 15%;
				margin: 6% 0% 0% 7%;
				text-align: center;
				z-index: 1;
			}

			.vennDiagramLeft {
				display: inline-block;
				position: absolute;
				border: 2px solid $blue;
				width: 65%;
				height: 75%;
				padding: 0%;
				margin: 8% 0% 2% 1%;
				border-radius: 3%;

				.sideLabel {
					float: left;
					text-align: center;
					margin: 15% 0% 0 -2%;
					width: min-content;
					font-size: 12px;
					letter-spacing: 3px;
					font-weight: 800;
					transform: rotate(-90deg);
				}

				.graphA {
					position: absolute;
					margin: 20% 0% 0% 30%;
				}

				.graphALabel {
					position: absolute;
					margin: 12vw 0 0 4.5vw;
					width: 9vw;
					text-align: center;
					color: $text-color;
					font-size: .85vw;
				}

				.commonGraphA {
					position: absolute;
					margin: 20% 0% 0% 65%;
				}

				.commonGraphB {
					position: absolute;
					margin: 20% 0% 0% 80%;
				}

				.commonGraphLabel {
					position: absolute;
					margin: 12vw 0 0 16.5vw;
					width: 9vw;
					text-align: center;
					color: $text-color;
					font-size: 0.85vw;
				}
			}

			.vennDiagramRightLabel {
				display: inline-block;
				position: absolute;
				background-color: $red;
				border-radius: 4px;
				color: #FFFFFF;
				padding: 0.5rem;
				width: 15%;
				margin: 0.5% 0% 0% 75%;
				text-align: center;
				z-index: 1;
			}

			.vennDiagramRight {
				position: relative;
				border: 2px solid $red;
				width: 60%;
				height: 75%;
				padding: 0px;
				margin: 2% 2% 0% 1%;
				border-radius: 3%;
				float: right;

				.graphB {
					position: absolute;
					margin: 32% 0% 0% 60%;
				}

				.graphBLabel {
					position: absolute;
					margin: 15vw 0 0 12vw;
					width: 9vw;
					text-align: center;
					color: $text-color;
					font-size: 0.85vw;
				}
			}

			.vennLabel {
				font-size: 12px;
				position: absolute;
				margin: 53% 0% 0% 3%
			}
		}

		.rightDiv {
			position: absolute;
			background-color: #ffffff;
			width: 53.2%;
			margin: 0.1vw 0 0 44.35vw;
			height: 26.2vw;
			vertical-align: top;

			.rightContainerA {
				position: absolute;
				border: 2px solid $border-color;
				width: 47%;
				height: 11.25vw;
				margin: 1.05vw 0 0 0.4vw;
				border-radius: 4px;

				.recharts-surface {
					width: 100%;
					height: 100%;
				}
			}

			.rightContainerB {
				position: absolute;
				border: 2px solid $border-color;
				width: 47%;
				height: 11.25vw;
				margin: 2% 0 0 25.5vw;
				border-radius: 4px;

				.commonStockDiv {
					background-color: $green;
					position: absolute;
					width: 24%;
					height: 5.5vw;
					border-radius: 90%;
					padding: 6.75% 0 4% 4%;
					margin: 15.7% 2.25% 4.5% 4.5%;
					float: left;

					.commonStockIcon {
						position: absolute;
						margin: 3% 0 0 7%;
						width: 50%;
					}
				}

				.commonStock {
					float: right;
					font-size: 2.7vw;
					margin: 18% 3% 0 0;
					font-weight: 550;
					direction: rtl;
				}
			}

			.rightContainerC {
				position: absolute;
				border: 2px solid $border-color;
				width: 47%;
				height: 11.25vw;
				margin: 14.2vw 0 0 0.4vw;
				border-radius: 4px;

				.unCommonStockDiv {
					background-color: $blue;
					position: absolute;
					width: 24%;
					height: 5.5vw;
					border-radius: 90%;
					padding: 6.75% 0 4% 4%;
					margin: 15.7% 2.25% 4.5% 4.5%;
					float: left;

					.unCommonStockIcon {
						position: absolute;
						margin: 3% 0 0 3%;
						width: 60%;
					}
				}

				.unCommonStockA {
					float: right;
					position: absolute;
					font-size: 2.7vw;
					font-weight: 550;
					margin: 1vw 0 0 11.5vw;
					direction: rtl;
					display: inline;
				}

				.totalStockA {
					float: right;
					position: absolute;
					font-size: 2.7vw;
					margin: 25% 0 0 62%;
					font-weight: 550;
					direction: rtl;
				}
			}

			.rightContainerD {
				position: absolute;
				border: 2px solid $border-color;
				width: 47%;
				height: 11.25vw;
				margin: 14.2vw 0 0 25.5vw;
				border-radius: 4px;

				.unCommonStockDiv {
					background-color: $red;
					position: absolute;
					width: 24%;
					height: 5.5vw;
					border-radius: 90%;
					padding: 6.75% 0 4% 4%;
					margin: 15.7% 2.25% 4.5% 4.5%;
					float: left;

					.unCommonStockIcon {
						position: absolute;
						margin: 3% 0 0 3%;
						width: 60%;
					}
				}

				.unCommonStockB {
					float: right;
					position: absolute;
					font-size: 2.7vw;
					margin: 1vw 0 0 11.5vw;
					font-weight: 550;
					direction: rtl;
					display: inline;
				}

				.totalStockB {
					float: right;
					position: absolute;
					font-size: 2.7vw;
					margin: 6vw 0 0 14.5vw;
					font-weight: 550;
					direction: rtl;
					display: inline;
				}

			}

			.text {
				float: right;
				font-size: 35.5%;
				direction: ltr;
				word-spacing: 0.25rem;
				color: $text-color;
			}
		}

	}

	.tableLabel {
		font-size: 15x;
		font-weight: 600;
		color: $text-color;
		word-spacing: 5px;
		margin: 0px 40px 0px 40px;
	}

	.tableScroll {
		position: relative;
		background-color: #ffffff;
		height: 600px;
		display: block;
		overflow: auto;
		margin: 1.1vw 1vw 0 1vw;
		border: 3px solid $border-color;

		.styledTable {
			width: 100%;
			font-size: 20px;
			border: 2px solid $border-color;
			background-color: #ffffff;
			border-collapse: collapse;

			.tableHeading {
				background-color: #ffffff;
				font-size: 18px;
				border-bottom: 3px solid #C7C8C8;
				padding: 10px 0 5px 20px;
				text-align: left;
				color: $text-color;
				position: sticky;
				top: 0px;

				.sortDefault:before,
				.sortDefault:after {
					border: 5px solid transparent;
					content: "";
					display: block;
					height: 0;
					right: 5px;
					top: 50%;
					position: absolute;
					width: 0;
				}

				.sortDefault:before {
					border-bottom-color: #666;
					margin-top: -11px;
				}

				.sortDefault::after {
					border-top-color: #666;
					margin-top: 3px;
				}

				.sortAscending:before,
				.sortAscending:after {
					border: 5px solid transparent;
					content: "";
					display: block;
					height: 0;
					right: 5px;
					top: 50%;
					position: absolute;
					width: 0;
				}

				.sortAscending:before {
					border-bottom-color: #666;
					margin-top: -11px;
					opacity: 30%;
				}

				.sortAscending:after {
					border-top-color: #666;
					margin-top: 3px;
				}

				.sortDescending:before,
				.sortDescending:after {
					border: 5px solid transparent;
					content: "";
					display: block;
					height: 0;
					right: 5px;
					top: 50%;
					position: absolute;
					width: 0;
				}

				.sortDescending:before {
					border-bottom-color: #666;
					margin-top: -11px;
				}

				.sortDescending:after {
					border-top-color: #666;
					margin-top: 3px;
					opacity: 30%;
				}
			}

			.tableHeading:hover {
				cursor: pointer;
			}

			.tableRow {
				.tableData {
					font-size: 1vw;
					padding: 5px 5px 5px 20px;

					.icons {
						float: left;
						margin: 2px 0 0 6px;
						vertical-align: top;
					}

					.percentageLeft {
						float: right;
						font-size: 0.9vw;
						color: #5275E9;
						font-weight: bold;
					}

					.percentageRight {
						float: right;
						font-size: 0.9vw;
						color: #F8432B;
						font-weight: bold;
					}

				}

				.progressColoumn {
					width: 35%;
					padding: 0 30px 0 17px;
				}

			}

			.tableRow:nth-child(even) {
				background-color: #efeff0;
			}
		}
	}

	.loader {
		border: 6px solid $border-color;
		border-top: 6px solid #3498db;
		border-radius: 50%;
		width: 90px;
		height: 90px;
		margin: auto;
		animation: spin 2s linear infinite;
	}

	@keyframes spin {
		0% {
			transform: rotate(0deg);
		}

		100% {
			transform: rotate(360deg);
		}
	}

	.bar {
		text-align: center;
		vertical-align: center;
		line-height: 1vh;
		width: fit-content;

		.barGraphRight {
			display: inline-block;
			width: 20px;
			height: 7px;
			border-radius: 2px 0px 0px 2px;
			margin: 0.2px 0px;
			font-size: 2px;
		}

		.barGraphLeft {
			display: inline-block;
			width: 20px;
			height: 7px;
			border-radius: 0px 2px 2px 0px;
			margin: 0.2px 0px;
			font-size: 2px;
		}
	}
}

